#header{
    text-align: center;
}
.header-button{
    padding: 0;
    margin-top: 20px;
    display: none;
    width: 35px;
    height: 25px;
    background-color: rgb(151, 100, 199);
    border: none;
    border-radius: 10px;
}
.nav{
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
    -webkit-box-flex: 1;
        -ms-flex-positive: 1;
            flex-grow: 1;
    margin-bottom: 20px;
}
.nav a img{
    opacity: 0.5;
}
.nav a{
   
    padding-top: 30px;
    -webkit-box-flex: 1;
        -ms-flex-positive: 1;
            flex-grow: 1;
    text-decoration: none;
    color: rgb(62, 62, 62);
    -webkit-transition: background-color 0.1s linear;
    transition: background-color 0.1s linear;
}
.current{
    background-color: rgb(229, 229, 229);
    -webkit-box-shadow: inset #9d426b  0px  5px  0px 0px;
            box-shadow: inset #9d426b  0px  5px  0px 0px;
}
#header .logo{
    
    display: inline-block;
    width:165px;
    padding-top: 25px;
    margin-bottom:5% ;
}
.nav a:hover{
    background-color: #9d426b;
    color: white;
}
.nav a:hover img{
    opacity: 1;
    filter: invert(100%); 
  -webkit-filter:invert(100%);
}
@media all and (max-width:991px ){
