.big-img{
    outline: 6px solid white;
    /* border:5px solid white ; */
    background-color: white;
    /* padding:  5px 6px 5px 5px; */
    position: relative;
}
.slider-this-bimg{
    display: inline-block;
}
.slider-text{
    
    top: 140px;
    margin-left: -99%; 
    color: rgba(255, 255, 255, 0.847);
    background-color: rgba(0, 0, 0, 0.836);
    position: relative;
    width: 98%;
    /* word-break:break-all; */
    white-space: normal; 
    display: inline-block;
    font-style: italic;
    /* margin: 0 10px; */
}
.slider-button-left{
    -webkit-transition: 
        left 0.5s ease-in 0s,
        opacity 0.5s ease-in 0s,
        margin 0.5s linear;
    transition: 
        left 0.5s ease-in 0s,
        opacity 0.5s ease-in 0s,
        margin 0.5s linear;
    display: inline-block;
    position: absolute;
    opacity: 0.0;
    width: 100px;
    height: 100px;
    content: " ";
    top: 370px;
    left: 15%;
    background: url(data:image/png;base64,) 0 center no-repeat;
    cursor: pointer;
    margin-top: 0px;
    z-index: 5;
}
.slider:hover  .slider-button-left{
    left: 20.7%;
    opacity: 0.9;
}
.slider-button-left:hover{
    opacity: 1;
}
.slider-button-right{
    -webkit-transition: 
        right 0.5s ease-in 0s,
        opacity 0.5s ease-in 0s,
        margin 0.5s linear;
    transition: 
        right 0.5s ease-in 0s,
        opacity 0.5s ease-in 0s,
        margin 0.5s linear;
    display: inline-block;
    position: absolute;
    opacity: 0.0;
    width: 100px;
    height: 100px;
    content: " ";
    top: 370px;
    right: 12.3%;
    background: url(data:image/png;base64,) 0 center no-repeat;
    cursor: pointer;
    margin-top: 0px;
    z-index: 5;
}
.slider:hover  .slider-button-right{
    right: 17.9%;
    opacity: 0.9;
}
.slider-button-right:hover{
    opacity: 1;
}
.slider-buttons-move{
    -webkit-transition: margin 0.5s linear;
    transition: margin 0.5s linear;
    margin-top: 300px;
}
.sm-imgs{
    margin-left: auto;
    margin-right: auto;
    border: 5px solid white;
    /* border-left: 5px solid white;
    border-right: 5px solid white; */
}
